import { Injectable, ApplicationRef } from '@angular/core';
import { MatDialog } from '@angular/material';
import { ConfirmDialogComponent } from '../components/confirm-dialog/confirm-dialog.component';
import { MatSnackBar } from '@angular/material';
import _ from 'lodash';

/**
 * Compone uno stack di elementi eseguibili (funzioni o oggetti con metodo di run)
 * da eseguire in sequenza. Al termine esegue la callback definita nel
 * metodo run (se specificata)
 * @param {object|string} [o]
 * @constructor
 */
export class Composer {
  constructor(options: any) {
    this._options = options || {};
  }
  private _nextMethod: string = null;
  private _stack: Array<any> = new Array<any>();
  private _options: any = null;
  private _exit = false;
  private _step = 0;
  private _getStep = () => {
    const self = this;
    const step = (!self._exit && self._step < self._stack.length) ? self._stack[self._step] : null;
    self._step++;
    return step;
  }
  // if (!_.isObject(o)) o = {nextMethod: _.isString(o) ? o : 'exec'};
  // this._nextMethod = o.nextMethod || 'exec';
  // this._stack = [];
  // this.options = o;
  // this.exit = false;
  use = (step) => {
    const self = this;
    self._stack.push(step);
    return self;
  }
  run = (cb) => {
    cb = cb || (() => {});
    const self = this;
    self._step = 0;
    if (self._stack.length <= 0) { return cb(self._options); }
    (function next() {
      const step = self._getStep();
      if (!step) {
        return cb(self._options);
      } else if (_.isFunction(step)) {
        step.call(self, next, self._options);
      } else if (_.isFunction(step[self._nextMethod])) {
        step[self._nextMethod](next, self._options);
      }
    })();
  }
}

const TYPES = {
  string: 'string',
  number: 'number',
  date: 'date',
  boolean: 'bool',
  object: 'object'
};

@Injectable()
export class UtilsService {
  constructor(public dialog: MatDialog,
              public snackBar: MatSnackBar,
              private app: ApplicationRef) { }
  storage = {
    set: (key: string, o: object) => {
      const o_str = o ? JSON.stringify(o) : '';
      localStorage.setItem(key, o_str);
    },
    get: (key: string) => {
      const o_str = localStorage.getItem(key) || '';
      return o_str ? JSON.parse(o_str) : null;
    },
    del: (key: string) => {
      localStorage.removeItem(key);
    }
  };
  modalActive = false;
  overpage: any = {};
  idle = false;
  err = null;
  static _s4() {
    return Math.floor((1 + Math.random()) * 0x10000).toString(16).substring(1);
  }
  static _isText(txt) {
    return _.isString(txt) && !_.startsWith(txt || '', '<!');
  }
  clone(o) {
    if (!_.isObject(o)) { return o; }
    const str = JSON.stringify(o);
    return JSON.parse(str);
  }
  raiseChange() {
    this.app.tick();
  }
  openOverpage(type = null, o = null) {
    this.overpage.back = null;
    this.overpage.type = type;
    this.overpage.options = o || {};
    this.idle = false;
  }
  closeOverpage() {
    this.openOverpage();
  }
  backOverpage() {
    if (_.isFunction(this.overpage.back)) {
      this.overpage.back();
    } else if (_.isObject(this.overpage.back)) {
      this.openOverpage(this.overpage.back.type, this.overpage.back.options);
    }
  }
  overpageBack(o) {
    const self = this;
    setTimeout(() => {
      self.overpage.back = o;
    }, 400);
  }
  noop() {}
  checkUrl(...parts: any[]) {
    const args = Array.prototype.slice.call(arguments);
    const url = [];
    args.forEach(part => {
      part = part.replace(/:\/\//, '{ECHO-PDL}');
      part.split(/\//).forEach((p) => {
        if (p === '..') {
          url.pop();
        } else if (p) {
          url.push(p.replace(/{ECHO-PDL}/, '://'));
        }
      });
    });
    return url.join('/');
  }
  private _enc(v) {
    return (v + '');
  }
  format(str, o = {}) {
    str = str || '';
    _.keysIn(o).forEach(pn => {
      const rgx = new RegExp('{' + pn + '}', 'g');
      str = str.replace(rgx, o[pn] || '');
    });
    return str;
  }
  getUrlParams(o) {
    const self = this;
    const params_cll = [];
    if (o) {
      _.keys(o).forEach(pn => params_cll.push(self._enc(pn) + '=' + self._enc(o[pn])));
    }
    return params_cll.length ? '?' + params_cll.join('&') : '';
  }
  parseUrlId(url: string, cb?: Function) {
    const pos = (url || '').lastIndexOf('/');
    const part_url = (pos > 1) ? url.slice(0, pos) : url;
    const part_id = (pos > 1) ? url.slice(pos + 1) : null;
    if (cb) { cb(part_url, part_id); }
    return part_url;
  }
  upperFirst(txt: string) {
    if ((txt || '').length > 0) {
      txt = txt.slice(0, 1).toUpperCase() + txt.slice(1);
    }
    return txt;
  }
  getErrorMessage(err: any, def?) {
    def = def || 'Generic error!';
    if (_.isString(err)) { return err; }
    if (_.isObject(err)) {
      if (err.error && _.isString(err.error) && UtilsService._isText(err.error)) { return err.error; }
      if (err.error && _.isObject(err.error) && UtilsService._isText(err.error.message)) { return err.error.message; }
      if (err.status === 0) { return 'Server seems to be down!'; }
      if (err.message) { return err.message; }
      if (err.data && _.isString(err.data)) { return err.data; }
      if (_.isObject(err.data)) { return JSON.stringify(err.data); }
      if (err && err.status < 0 && _.isObject(err.config)) {
        return '"' + err.config.method + '" on "' + err.config.url + '" has caused an error';
      }
      if (err.statusText) { return err.status ? err.status + ' - ' + err.statusText : err.statusText; }
      try {
        return JSON.stringify(err) || def;
      } catch (ex) {}
    }
    return def;
  }
  guid() {
    return UtilsService._s4() + UtilsService._s4() + '-' + UtilsService._s4() + '-' +
      UtilsService._s4() + '-' + UtilsService._s4() + '-' + UtilsService._s4() + UtilsService._s4() + UtilsService._s4();
  }
  getNewName(list: any[], template: string, prop?: string) {
    prop = prop || 'name';
    let name = template;
    let index = 0;
    while (!!_.find(list, i => i[prop] === name)) {
      name = template + '(' + (++index) + ')';
    }
    return name;
  }
  empty(o: object, pn?: any) {
    if (o) {
      if (pn) {
        _.isArray(pn) ? pn.forEach(k => delete o[k]) : delete o[pn];
      } else {
        _.keys(o).forEach(k => delete o[k]);
      }
    }
  }
  getDataType(type) {
    type = type || '';
    switch (type.toLowerCase()) {
      case 'int':
      case 'tiny':
      case 'tinyint':
      case 'short':
      case 'smallint':
      case 'int24':
      case 'long':
      case 'longlong':
      case 'bigint':
      case 'integer':
      case 'number':
      case 'numeric':
      case 'float':
      case 'real':
      case 'double':
      case 'decimal':
      case 'newdecimal':
      case 'currency':
      case 'money':
        return TYPES.number;
      case 'date':
      case 'time':
      case 'time2':
      case 'year':
      case 'newdate':
      case 'datetime':
      case 'datetime2':
      case 'timestamp':
      case 'timestamp2':
      case 'interval':
      case 'smalldatetime':
      case 'datetimeoffset':
        return TYPES.date;
      case 'string':
      case 'var_string':
      case 'character':
      case 'nchar':
      case 'nvarchar':
      case 'char':
      case 'varchar':
      case 'text':
      case 'xml':
      case 'json':
        return TYPES.string;
      case 'bit':
      case 'bool':
      case 'boolean':
        return TYPES.boolean;
      default:
        return TYPES.object;
    }
  }
  private _isText(v, o) {
    if (o.textSeparator) {
      const txt_rgx = new RegExp('^' + o.textSeparator + '.*' + o.textSeparator + '$', 'g');
      return txt_rgx.test(v || '');
    }
  }
  private _isNumeric(v) {
    const fl = parseFloat(v) + '';
    const fl_rgx = new RegExp('^' + fl.split('.')[0] + '\\.0+$', 'g');
    return (fl === (v || '') + '' || fl_rgx.test(v || ''));
  }
  private _isDate(v) {
    // TODO: auto riconoscimento di stringhe = date
    return false;
  }
  private _isBoolean(v) {
    // TODO: auto riconoscimento di stringhe = bool
    return (v === 'true' || v === 'false');
  }
  decodeType(v, o) {
    if (v) {
      if (this._isText(v, o)) { return TYPES.string; }
      if (this._isNumeric(v)) { return TYPES.number; }
      if (this._isDate(v)) { return TYPES.date; }
      if (this._isBoolean(v)) { return TYPES.boolean; }
    }
    return TYPES.string;
  }
  getTypeIcon(type) {
    switch (this.getDataType(type)) {
      case TYPES.string: return 'font_download';
      case TYPES.boolean: return 'check_box';
      case TYPES.date: return 'event';
      case TYPES.number: return 'looks_two';
      case TYPES.object:
      default: return 'texture';
    }
  }
  confirm(o, cb) {
    const confirm = this.dialog.open(ConfirmDialogComponent, {
      width: o.width || '340px',
      data: o
    });
    confirm.afterClosed().subscribe(result => cb(result));
  }
  snack(message: string, duration?: number) {
    this.snackBar.open(message, null, {
      duration: duration || 3000
    });
  }
  error(err) {
    if (!err) { return; }
    // console.error(err);
    this.err = this.getErrorMessage(err);
  }
  getOrigin() {
    return window['origin'] ||
      window.location.origin ||
      window.location.protocol + '//' + window.location.hostname + (window.location.port ? ':' + window.location.port : '');
  }
  clearPath() {
    const url = window.location.protocol + '//' + window.location.host + window.location.pathname;
    window.history.pushState({path: url}, '', url);
  }
  validate(str: string, rgxstr: string = null) {
    const rgx = (rgxstr) ? new RegExp(rgxstr) : null;
    const valid = (rgx) ? rgx.test(str) : true;
    return valid && str && _.isString(str) && str.trim().length > 1;
  }
  getUrlGuid() {
    const query = window.location.search || '';
    const rgx = /^\?map=(.*)$/g;
    const m = query ? rgx.exec(window.location.search || '') : null;
    return m ? m[1] : null;
  }
  compose(o: any = null) {
    return new Composer(o);
  }
}



