import { Injectable } from '@angular/core';
import {UtilsService} from './utils.service';
import _ from 'lodash';
declare const google: any;

@Injectable()
export class MapsService {

  constructor(private u: UtilsService) {
  }
  elements = [];
  getOptions() {
    return {
      zoom: 14,
      center: new google.maps.LatLng(43.7681469, 11.2527254),
      mapTypeId: google.maps.MapTypeId.ROADMAP
    };
  }
  onElement(name: string, cb: (m: any) => any) {
    const m = name ? _.find(this.elements, (e) => e.name === name) : null;
    if (m) {
      cb(m);
    }
  }
  create(cb, dommap = null, domfinder = null, o = null) {
    const self = this;
    o = o || self.getOptions();
    cb = cb || self.u.noop;
    dommap = dommap || 'map-canvas';
    // domfinder = domfinder || 'map-finder';
    const context: any = {
      options: o,
      map: new google.maps.Map(document.getElementById(dommap), o),
      directionsService: new google.maps.DirectionsService,
      directionsDisplay: new google.maps.DirectionsRenderer,
      searchBox: {}
    };

    // Crea il box per la ricerca e lo sincronizza con la mappa
    if (domfinder) {
      const input = document.getElementById(domfinder);
      context.searchBox = new google.maps.places.SearchBox(input);
      context.map.controls[google.maps.ControlPosition.TOP_LEFT].push(input);
      context.directionsDisplay.setMap(context.map);
      context.map.addListener('bounds_changed', function () {
        context.searchBox.setBounds(context.map.getBounds());
      });
      context.searchBox.addListener('places_changed', function () {
        const places = context.searchBox.getPlaces();
        if (places.length) {
          // self.ndo6.center(places[0].geometry.location);
        }
      });
    }

    google.maps.event.addListenerOnce(context.map, 'idle', function () {
      cb(context);
    });
    return context;
  }
  getPosition(pos) {
    if (pos) {
      if (pos.coords) { pos = pos.coords; }
      return {
        latitude: pos.latitude ? pos.latitude : (_.isFunction(pos.lat) ? pos.lat() : null),
        longitude: pos.longitude ? pos.longitude : (_.isFunction(pos.lng) ? pos.lng() : null)
      };
    }
  }
  /**
   * Restituisce un latlng
   * @param pos
   * @returns {google.maps.LatLng}
   */
  getLatLng(pos) {
    const p = this.getPosition(pos);
    return p ? new google.maps.LatLng(p.latitude, p.longitude) : null;
  }
  clearMapElements() {
    this.elements.forEach(e => e.setMap(null));
    this.elements.splice(0);
  }
  deleteMapElement(m) {
    m.setMap(null);
    _.remove(this.elements, e => e === m);
  }
  getDistanceLabel(m: number, cb) {
    if (m >= 1000) {
      cb((m / 1000).toFixed(2), 'km');
    } else {
      cb(m + '', 'm');
    }
  }
}





