import { Injectable, EventEmitter } from '@angular/core';
import {MapsService} from './maps.service';
import {UserSettingsService} from './user-settings.service';
import {UtilsService} from './utils.service';
import {LogService} from './log.service';
import {InteractionService, InteractionEvent} from './interaction.service';
import {Ndo6IconsService} from './ndo6-icons.service';
import _ from 'lodash';
declare const google: any;

export class Ndo6Event {
  type: string;
  data: any;
  constructor(type: string, data: any = null) {
    this.type = type;
    this.data = data;
  }
}

export interface IPosition {
  longitude: number;
  latitude: number;
}

export interface IMarkerPosition {
  lat(): number;
  lng(): number;
}

export interface IMarker {
  ndo6: any;
  label: any;
  timestamp: number;
  position: IMarkerPosition;
  getPosition(): any;
  setPosition(ll: any);
  setLabel(lb: any);
  setTitle(tt: string);
}

export interface IDistanceArgs {
  origin: any;
  destination: any;
  travelMode: string;
  transitOptions?: any;
  drivingOptions?: any;
  avoidHighways?: boolean;
  avoidTolls?: boolean;
}

export interface IDistanceService {
  getDistanceMatrix(di: any, cb: (response, status) => any);
}

export interface IMapElement {
  position: any;
  name: string;
  type: string;
  content: any;
}


const GEOLOCATION_OPTIONS = {
  maximumAge: 10,
  enableHighAccuracy: false,
  timeout: 10000
};

const MARKERS = {
  owner: 'owner',
  point: 'point'
};

const MAP_ELEMENT_TYPES = {
  point: 'point',
  way: 'way'
};

const TRAVEL_MODES = {
  bicycling: 'BICYCLING',
  driving: 'DRIVING',
  transit: 'TRANSIT',
  walking: 'WALKING'
};

@Injectable()
export class Ndo6Service {
  events: EventEmitter<Ndo6Event> = new EventEmitter();
  constructor(private maps: MapsService,
              private user: UserSettingsService,
              private u: UtilsService,
              private log: LogService,
              private interaction: InteractionService,
              private icons: Ndo6IconsService) {
    const self = this;
    self.interaction.events.subscribe((e: InteractionEvent) => {
      switch (e.code) {
        case 401:
          self.logout();
          break;
      }
    });
  }
  private positionChecker = null;
  mym: IMarker = null;
  private distanceService: any = null;
  session: any = {
    user: {},
    map: null,
    context: null
  };
  onfirstpos = false;
  followMarker = null;
  distance = {
    marker1: null,
    marker2: null,
    value: 0,
    text: '',
    um: '',
    label: ''
  };
  watchId = null;
  active = false;
  center = false;
  centerPoint: string = null;
  posErrorCounter = 0;
  clickOnMarker = null;
  pooling: any = null;
  markers = MARKERS;
  mapElements = MAP_ELEMENT_TYPES;
  init(cb) {
    const self = this;
    self.user.invitation = self.u.getUrlGuid();
    if (self.user.needInit()) {
      self.u.clearPath();
      self.u.overpage.options = {
        style: 'background',
        close: false,
        callback: cb
      };
      self.u.overpage.type = 'initial';
    } else {
      cb();
    }
  }
  checkInvite() {
    const self = this;
    if (self.user.inviteAccepted && self.user.invite) {
      if (self.user.isLogged()) {
        self.logout();
      }
      self.centerPoint = self.user.invite.point;
      self.login(self.user.invite.token, {
        name: self.user.invite.map,
        nick: self.user.settings.nick
      });
    }
  }
  getDistanceService() {
    this.distanceService = this.distanceService || new google.maps.DistanceMatrixService();
    return this.distanceService;
  }
  setZoom(zoom) {
    const self = this;
    if (!zoom || !self.session.context) { return; }
    const listener = google.maps.event.addListener(self.session.context.map, 'idle', () => {
      self.session.context.map.setZoom(zoom);
      google.maps.event.removeListener(listener);
    });
  }
  /**
   * centra la mappa
   */
  centerMap(pos: IMarker, zoom = null, finder = null) {
    const self = this;
    if (!self.session.context || !pos) { return; }
    // il centro  considerato pi in alto per
    // lasciare lo spazio al monitor
    const bounds = self.session.context.map.getBounds();
    if (!bounds) { return; }

    // Imposta il centro della mappa
    self.session.context.map.setCenter(pos.position);

    const mrk = finder ? finder() : null;
    if (mrk) {
      // Se ha trovato il marker lo anima
      mrk.setAnimation(google.maps.Animation.BOUNCE);
      setTimeout(function () {
        mrk.setAnimation(null);
      }, 1000);
    }
    self.setZoom(zoom);
  }
  private _checkGeoError(err) {
    const self = this;
    switch (err.code) {
      case 1: self.log.error('Geolocation permission denied'); break;
      case 2: self.log.error('Geolocation position unavailable'); break;
      case 3: self.log.warning('Geolocation timed out'); break;
      default: self.log.error(err);
    }
  }
  watchGeo(cb) {
    const self = this;
    self.watchId = navigator.geolocation.watchPosition(cb, function (err: any) {
      self._checkGeoError(err);
    }, GEOLOCATION_OPTIONS);
  }
  checkGeo() {
    const self = this;
    return new Promise((resolve, reject) => {
      if (!navigator.geolocation) {
        const err = 'Geolocation is not supported by this browser.';
        self.log.error(err);
        return reject(err);
      } else {
        navigator.geolocation.getCurrentPosition(resolve, function (err: any) {
          self._checkGeoError(err);
          reject(err);
        }, GEOLOCATION_OPTIONS);
      }
    });
  }
  reset() {
    if (this.positionChecker) {
      clearTimeout(this.positionChecker);
    }
    this.active = false;
    this.clickOnMarker = null;
  }
  samePos(p1, p2) {
    return p1 && p2 && p1 === p2;
  }
  samePosOf(m: IMarker, pos: IPosition) {
    return (m && pos && m.position.lat() === pos.latitude && m.position.lng() === pos.longitude);
  }
  validMarkerPos(m: IMarker) {
    return m && m.position.lat() !== 0 && m.position.lng() !== 0;
  }
  validPos(pos: any) {
    if ((pos || {}).coords) { pos = pos.coords; }
    return pos && pos.latitude !== 0 && pos.longitude !== 0;
  }
  setPos(pos: IPosition) {
    const self = this;
    if (!self.mym) {
      self.onfirstpos = true;
      self.mym = self.getMarker({
        type: MARKERS.owner,
        icon: self.icons.defaults.personIcon,
        // label: self.user.settings.nick[0],
        label: self.user.settings.nick,
        nick: self.user.settings.nick,
        owner: self.user.settings.nick,
        id: self.user.settings.id
      });
    }
    self.log.info('POSITION: ', pos);
    if (!self.samePosOf(self.mym, pos) && self.validPos(pos)) {
      const latlng = self.maps.getLatLng(pos);
      self.mym.setPosition(latlng);
      self.log.info('EMIT POSITION: ', pos);
      const p = self.maps.getPosition(pos);
      self.events.emit(new Ndo6Event('changepos', {
        latitude: p.latitude,
        longitude: p.longitude,
        id: self.user.settings.id
      }));
      const center = self.center || (self.followMarker === self.mym);
      if (center === true) {
        self.centerMap(self.mym);
        self.center = false;
      }
      if (self.onfirstpos) {
        self.events.emit(new Ndo6Event('firstpos'));
        self.onfirstpos = false;
      }
      self.refreshDistance(self.mym);
    }
  }
  checkPos() {
    const self = this;
    self.positionChecker = setTimeout(() => {
      self.checkGeo()
        .then((pos: any) => {
          self.posErrorCounter = 0;
          self.log.info('read position: ' + self.u.format('{latitude},{longitude}', pos.coords));
          self.setPos(pos);
          if (self.active) {
            self.checkPos();
          }
        }, err => {
          if (err) {
            self.posErrorCounter++;
          }
          self.checkPos();
        });
    }, self.user.settings.delay || 1000);
  }
  clearGeo() {
    const self = this;
    if (self.positionChecker) {
      clearTimeout(self.positionChecker);
    }
  }
  stopGeo() {
    const self = this;
    if (self.watchId) {
      navigator.geolocation.clearWatch(self.watchId);
    }
  }
  activateW(context) {
    const self = this;
    self.session.context = context;
    self.stopGeo();
    self.center = true;
    self.watchGeo(function(pos){
      self.setPos(pos);
    });
    self.active = true;
    self.checkState();
  }
  activate(context) {
    const self = this;
    self.session.context = context;
    self.clearGeo();
    self.center = true;
    self.checkPos();
    self.active = true;
    self.checkState();
  }
  logout(close = false, cb: () => any = null) {
    this.user.logdata();
    if (this.u.overpage.type !== 'map' || close) { this.u.closeOverpage(); }
    this.checkState(cb);
  }
  login(token, data, cb: () => any = null) {
    this.user.logdata(token, data);
    this.u.closeOverpage();
    this.checkState(cb);
  }
  resetDistance(m = null) {
    if (!m) {
      this.distance.marker1 = null;
      this.distance.marker2 = null;
    } else {
      if (this.distance.marker1 === m) { this.distance.marker1 = null; }
      if (this.distance.marker2 === m) { this.distance.marker2 = null; }
    }
    this.distance.label = '';
    this.distance.text = '';
    this.distance.um = '';
    this.distance.value = 0;
  }
  checkNick() {
    const self = this;
    if (self.mym && self.mym.label.text !== self.user.settings.nick) {
      self.mym.setLabel({
        text: self.user.settings.nick,
        color: '#111',
        fontSize: '12px',
        fontWeight: 'normal'
      });
    }
  }
  checkState(cb: () => any = null) {
    const self = this;
    cb = cb || self.u.noop;
    self.checkNick();
    // se esiste il token attiva il pool oppure connette il socket (secondo lo impostazioni)
    if (!!self.user.settings.token) {
      if (self.user.settings.socketMode) {
        // TODO: se non connesso connette il socket
        cb();
      } else if (!self.pooling) {
        // se non attivo accende il pooling
        self.pooling = setInterval(() => {
          self.interaction.getState((err, res) => {
            // self.log.info('read state: ', res, self.log.types.special);
            if (err) { return; }
            res = res || {};
            self.checkPositions(res.positions);
            self.checkElements(res.elements);
            self.checkMessages(res.messages);
            cb();
          });
        }, self.user.settings.poolingTime || 1000);
      }
    } else {
      // se attivo spenge il pooling
      if (self.pooling) {
        clearInterval(self.pooling);
        self.pooling = null;
      }
      // TODO: se connesso sconnette il socket
      // elimina gli oggetti sulla mappa
      self.clearMap();
      cb();
    }
  }
  clearMap() {
    const self = this;
    // elimina gli oggetti sulla mappa
    self.maps.clearMapElements();
    self.resetDistance();
    if (self.followMarker && self.followMarker !== self.mym) {
      self.followMarker = null;
    }
  }
  checkPositions(ps) {
    if (!_.isArray(ps)) { return; }
    const self = this;
    const mym: any = (self.mym || {})['ndo6'] || {};
    ps.forEach(function(pos){
      if (pos.id === mym.id || pos.owner === self.user.settings.nick) { return; }
      let ex = _.find(self.maps.elements, (e: any) => e.ndo6.owner === pos.owner && e.ndo6.type === MARKERS.owner);
      if (ex) {
        const latlng = self.maps.getLatLng(pos);
        ex.setPosition(latlng);
      } else {
        // pos.label = pos.owner[0] || '?';
        pos.label = pos.owner || '?';
        pos.type = MARKERS.owner;
        pos.icon = pos.icon || self.icons.defaults.personIcon;
        ex = self.getMarker(pos);
        self.maps.elements.push(ex);
      }
      if (ex === self.followMarker) {
        self.centerMap(ex);
      }
      self.refreshDistance(ex);
    });
  }
  checkElements(es) {
    if (!_.isArray(es)) { return; }
    const self = this;
    es.forEach(function(ele) {
      let ex = _.find(self.maps.elements, (e: any) => e.ndo6.type === ele.type && e.ndo6._id === ele._id);
      if (ex) {

      } else {
        ele.latitude = ele.content.position.latitude;
        ele.longitude = ele.content.position.longitude;
        ele.icon = ele.content.icon || self.icons.defaults.placeIcon;
        ele.label = ele.name;
        ele.draggable = !!ele.content.draggable;
        ex = self.getMarker(ele);
        self.maps.elements.push(ex);
      }
      if (!self.followMarker && !!self.centerPoint && ex.ndo6.name === self.centerPoint) {
        self.centerMap(ex);
      }
    });
  }
  checkMessages(ms) {
    if (!_.isArray(ms)) { return; }
    // TODO: messaggi
  }
  getMarker(info) {
    const self = this;
    if (!self.session.context) { return null; }
    const latlnt = self.maps.getLatLng(info);
    const draggable = !!info.draggable;
    const m = new google.maps.Marker({
      map: self.session.context.map,
      label: {
        text: info.label,
        color: '#111',
        fontSize: '12px',
        fontWeight: 'normal'
      },
      position: latlnt,
      title: info.title || info.description || info.nick,
      icon: self.icons.getIcon(info),
      draggable: draggable
    });
    m.ndo6 = {};
    _.extend(m.ndo6, info);
    m.ndo6.id = m.ndo6.id || self.u.guid();
    google.maps.event.addListener(m, 'click', function() {
      self.events.emit(new Ndo6Event('marker', m));
    });
    if (draggable) {
      google.maps.event.addListener(m, 'dragend', function () {
        self.events.emit(new Ndo6Event('marker_dragend', m));
      });
    }
    return m;
  }
  getNick(m) {
    if (m === this.mym) {
      return this.user.settings.nick;
    }
    return m.ndo6.owner;
  }
  getLastPos() {
    if (this.validMarkerPos(this.mym)) {
      return {
        latitude: this.mym.position.lat(),
        longitude: this.mym.position.lng(),
        timestamp: this.mym.timestamp
      };
    }
  }
  getCurrentPos() {
    const pos = (this.session || {}).context ? this.session.context.map.getCenter() : null;
    return pos ? {
      latitude: pos.lat(),
      longitude: pos.lng()
    } : null;
  }
  addToMap(ele: IMapElement) {
    const self = this;
    // console.log('NEW ELEMENT BY CLIENT:', ele);
    self.interaction.element(ele, (err) => {
      if (!err) {
        self.u.snack('Element shared on map.');
      }
    });
  }
  deleteElement(marker) {
    const self = this;
    self.interaction.deleteElement(marker.ndo6, (err) => {
      if (!err) {
        self.maps.deleteMapElement(marker);
        self.u.snack('Element deleted.');
      }
    });
  }
  refreshDistance(source: any = null) {
    const self = this;
    if (source && source !== self.distance.marker1 && source !== self.distance.marker2) { return; }
    if (self.distance.marker1 && self.distance.marker2) {
      self.distance.label = self.distance.marker1.ndo6.label + ' - ' + self.distance.marker2.ndo6.label;
      self.calcDistance({
        origin: self.distance.marker1,
        destination: self.distance.marker2,
        travelMode: TRAVEL_MODES.walking
      }, (err, data) => {
        if (err) { return self.log.error(err); }
        self.distance.value = data.distance;
        self.maps.getDistanceLabel(data.distance, (txt, um) => {
          self.distance.text = txt;
          self.distance.um = um;
        });
      });
    } else {
      self.distance.label = '';
    }
  }
  calcDistance(o: IDistanceArgs, cb) {
    const self = this;
    const origin = o.origin.getPosition();
    const destination = o.destination.getPosition();
    self.getDistanceService().getDistanceMatrix({
      origins: [origin],
      destinations: [destination],
      travelMode: o.travelMode || TRAVEL_MODES.walking,
      transitOptions: o.transitOptions,
      drivingOptions: o.drivingOptions,
      unitSystem: google.maps.UnitSystem.METRIC,
      avoidHighways: !!o.avoidHighways,
      avoidTolls: !!o.avoidTolls,
    }, (resp, status) => {
      switch (status) {
        case 'OK':
          //  The request is valid. This status can be returned even if no routes were found between any of the
          //    origins and destinations. See Element Status Codes for the element-level status information.
          let dist = 0;
          resp.rows[0].elements.forEach((e) => {
            dist += e.distance.value;
          });
          cb(null, {distance: dist});
          break;
        case 'INVALID_REQUEST':
          //  The provided request was invalid. This is often due to missing required fields.
          //    See the list of supported fields above.
        case 'MAX_ELEMENTS_EXCEEDED':
          //  The product of origins and destinations exceeds the per-query limit.
        case 'MAX_DIMENSIONS_EXCEEDED':
          //  Your request contained more than 25 origins, or more than 25 destinations.
        case 'OVER_QUERY_LIMIT':
          //  Your application has requested too many elements within the allowed time period. The request should
          //    succeed if you try again after a reasonable amount of time.
        case 'REQUEST_DENIED':
          //  The service denied use of the Distance Matrix service by your web page.
        case 'UNKNOWN_ERROR':
          //  A Distance Matrix request could not be processed due to a server error.
          //    The request may succeed if you try again.
          const err = 'Error on distance calc request (' + status + ')!';
          self.log.error(err);
          return cb(err);
      }
    });
  }
}



