@Component({
  selector: 'app-element-point',
  templateUrl: './elements/element-point.html'
})
export class MapElementPointComponent implements OnInit {
  constructor(private u: UtilsService,
              private ndo6: Ndo6Service,
              public graph: Ndo6IconsService) {
  }
  private template: IMapElement = {
    position: {},
    type: '',
    name: 'New Point',
    content: {}
  };
  info: any = null;
  ngOnInit() {
    const e: IMapElement = _.clone(this.template);
    e.type = this.ndo6.mapElements.point;
    e.content.desc = '';
    e.content.icon = this.graph.getDef('flag');
    e.content.color = this.graph.defaults.color;
    e.content.position = this.ndo6.getCurrentPos();
    e.content.draggable = false;
    this.u.overpage.options.element = e;
    this.info = e;
  }
  setIcon(idef) {
    const color = this.info.content.icon.color || this.graph.defaults.color;
    this.info.content.icon = this.graph.getDef(idef.code);
    if (this.info.content.icon.colors.indexOf(color) < 0) {
      this.info.content.icon.color = this.graph.defaults.color;
    } else {
      this.info.content.icon.color = color;
    }
  }
  setColor(color) {
    this.info.content.icon.color = color;
  }
}


