@Component({
  selector: 'app-overpage-invite',
  templateUrl: './overpage-invite.html'
})
export class OverpageInviteComponent implements OnInit {
  constructor(public u: UtilsService,
              private user: UserSettingsService,
              private interaction: InteractionService,
              private maps: MapsService) {
  }
  inviteState = {
    valid: false,
    error: ''
  };
  elements = [];
  ngOnInit() {
    this.u.overpage.options.element = {
      emails: '',
      message: this.user.settings.nick + ' invite you in map "' + this.user.settings.map + '"',
      point: null,
      age: 2
    };
    this.inviteState.valid = false;
    this.elements = this.maps.elements;
  }
  refreshState() {
    this.inviteState.valid = /[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*/g
      .test(this.u.overpage.options.element.emails);
  }
  invite() {
    const self = this;
    self.inviteState.error = '';
    self.u.idle = true;
    this.interaction.invite(this.u.overpage.options.element, (err, res) => {
      self.u.idle = false;
      if (err) {
        self.inviteState.error = this.u.getErrorMessage(err);
      } else if (((res || {}).errors || []).length) {
        self.inviteState.error = _.map(res.errors, (e) => this.u.getErrorMessage(err)).join('\n');
      } else {
        self.u.snack('Invitation sended successfully');
        this.u.closeOverpage();
      }
    });
  }
}



